































































































































.alpheios-alignment-editor-modal-options {
  .alpheios-modal-container {
    width: 700px;
  }
  .alpheios-modal-body {
    // max-height: 700px;
    margin: 0 0 20px;

    border: 0;
    border-top: 2px solid #ddd;
    padding: 10px 0 0 0;
  }

  .alpheios-alignment-editor-modal-options-block {
      padding: 10px;
      text-align: center;
  }

  .alpheios-preset-item-value-block {
    display: inline-block;
    border: 2px solid #e8e8e8;
    padding: 20px;
    background: #ddd;
    margin:10px;
    cursor: pointer;
    min-width: 110px;

    font-weight: bold;
    border-radius: 10px;

    .alpheios-preset-item-value-block__text {
      display: block;
      text-align: center;
    }

    .alpheios-preset-item-value-block__icons {
      display: block;
      text-align: center;

      .alpheios-alignment-icon {
        display: inline-block;
        width: 25px;
        height: 25px;
        cursor: pointer;

        svg {
          width: 100%;
          height: 100%;
          display: block;
          fill: #000;
        }
      }
    }


    &.alpheios-preset-item-value-block-selected {
      border-color: #588598;
      background: #46788d;
      color: #fff;

      .alpheios-preset-item-value-block__icons .alpheios-alignment-icon {
        svg {
          fill: #fff;
        }
      }
    } 
  }
}
